  import demobasic from './demo/basic.md';
import demoalign from './demo/align.md';
import demoflex from './demo/flex.md';
import demogutter from './demo/gutter.md';
import demojustify from './demo/justify.md';
import demooffset from './demo/offset.md';
import demopush_pull from './demo/push_pull.md';
import demoxs_sm_md_lg from './demo/xs_sm_md_lg.md';
import demoother from './demo/other.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demoalign,demoflex,demogutter,demojustify,demooffset,demopush_pull,demoxs_sm_md_lg,demoother
    }
  };
  
      export default {
        mixins:[mixin]
      }
